import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { CartaoPage } from './../pages/cartao/cartao';
import { SobrePage } from './../pages/sobre/sobre';
import { MedicosPage } from './../pages/medicos/medicos';
import { HistoricoPage } from './../pages/historico/historico';
import { FichaMedicaPage } from './../pages/ficha-medica/ficha-medica';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    FichaMedicaPage,
    HistoricoPage,
    MedicosPage,
    CartaoPage,
    SobrePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/cartao/cartao.module#CartaoPageModule', name: 'CartaoPage', segment: 'cartao', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sobre/sobre.module#SobrePageModule', name: 'SobrePage', segment: 'sobre', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/medicos/medicos.module#MedicosPageModule', name: 'MedicosPage', segment: 'medicos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/historico/historico.module#HistoricoPageModule', name: 'HistoricoPage', segment: 'historico', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ficha-medica/ficha-medica.module#FichaMedicaPageModule', name: 'FichaMedicaPage', segment: 'ficha-medica', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    FichaMedicaPage,
    HistoricoPage,
    MedicosPage,
    CartaoPage,
    SobrePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



